
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'

export default {
name: 'getMoney',
components: {
XInput,
Group,
CheckIcon
},
data () {
return {
value: '',
baseInfo: {},
type: ''
}
},
created () {
this.getInfo()
},
methods: {
getAll () {
this.value = this.baseInfo.balance
},
sendData(){
console.log(this.type)
if(!this.value){
this.$vux.toast.show({
text: '请输入提现金额',
position: 'middle',
type: 'warn'
})
return
}
if(parseInt(this.value) > this.baseInfo.balance){
this.$vux.toast.show({
text: '提现金额不能大于余额',
position: 'middle',
type: 'warn'
})
return
}
if(!this.type){
this.$vux.toast.show({
text: '请输入选择提现方式',
position: 'middle',
type: 'warn'
})
return
}
this.$http.fetchPost('/merchant/center/update/balance/withdraw',{
accountType: this.type,
amount: this.value,
merchantId: this.$store.state.merchantId
}).then((res)=>{
if(res.data.code == 200) {
this.$vux.toast.show({
text: '提现成功',
position: 'middle'
})
this.$router.go(-1)
}else{
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
},
getInfo () {
this.$http.fetchGet('/merchant/center/get/balance/withdraw',{merchantId: this.$store.state.merchantId}).then((res)=>{
this.baseInfo = res.data.data
})
},
getBack(){
this.$router.go(-1)
}
}
}



